import React from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";

const PassengerList = ({ passengers, flightName, ancillaries }) => (
  <table className="table">
    <thead>
      <tr>
        <th>PNR</th>
        <th>Name</th>
        <th>Ancillary Service</th>
        <th>Seat No.</th>
        <th>Passport</th>
        <th>Date of Birth</th>
        <th>Address</th>
      </tr>
    </thead>
    <tbody>
      {passengers.map((passenger) => {
        const ancillaryPresent =
          passenger.ancillary.length > 0 &&
          ancillaries.id &&
          passenger.ancillary.filter((a) => ancillaries.value[a]).length > 0;
        return (
          <tr key={passenger.id}>
            <td>
              {passenger.pnr}
              <Link
                style={{ marginLeft: 10 }}
                to={"/admin/passenger/" + flightName + "/" + passenger.pnr}
                className="btn btn-sm btn-outline-primary"
              >
                update
              </Link>
            </td>
            <td>{passenger.name}</td>
            <td>
              {ancillaryPresent
                ? passenger.ancillary.map(
                    (a) =>
                      ancillaries.value[a] && (
                        <div key={a}>{ancillaries.value[a].value}</div>
                      )
                  )
                : "NIL"}
            </td>
            <td>{passenger.seat ? passenger.seat : "--"}</td>
            <td>
              {passenger.passport && true === passenger.passport ? "YES" : "NO"}
            </td>
            <td>{passenger.dob ? passenger.dob : "--"}</td>
            <td>{passenger.address ? passenger.address : "--"}</td>
          </tr>
        );
      })}
    </tbody>
  </table>
);

PassengerList.propTypes = {
  passengers: PropTypes.array.isRequired,
  flightName: PropTypes.string.isRequired,
  ancillaries: PropTypes.object.isRequired,
};

export default PassengerList;
